<script>
/* eslint no-unused-vars: warn */
/* eslint no-undef: warn */
import { GTMPush } from '@Submodule/UTILS';
import dictionary from '@Project/data/dictionary.json';
import { xOut as XOut } from '@Submodule/UI-Svelte/';
import { DimensionFilter, Filters } from '@Project/store.js';
import { fieldValues } from '@Project/scripts/data.js';
import { isWorking } from '@Project/app.js';
import { get } from 'svelte/store';

export let section;
let inputs = [];
let data = [...fieldValues[section]].sort().map(v => {
    return {
        key: v,
        display: dictionary[v].display
    }
});

export let selected = []; // changes committed to by submitting the form
export let dirtySelected = []; // changes not yet committed to
export let isDirty;
export let clearAll = function(){
    isWorking(true);
    function _clearAll(){
        inputs.forEach(ip => ip.checked = false);
        formSubmit();
        dirtySelected = [];
    }
    if ( window.requestIdleCallback ){
        requestIdleCallback(_clearAll,{timeout: 500});
    } else {
        setTimeout(_clearAll);
    }
};
$:isDirty = !( 
        selected.every(item => dirtySelected.includes(item)) &&
        dirtySelected.every(item => selected.includes(item))
    );

let isOpen = false;
DimensionFilter.subscribe(v => {
 isOpen = v == section;
});
function inline(key){
    return dictionary[key].alwaysCaps ? dictionary[key].display : dictionary[key].display.toLowerCase();
}
function plural(key){
    var _inline = inline(key);
    return dictionary[key].pluralS ? _inline + 's' : _inline;
}
function keydownHandler(){

}
function changeHandler(e){
 if ( this.checked ){
    dirtySelected = [...dirtySelected, this.name];
    console.log(dirtySelected,e,this);
 } else {
    let i = dirtySelected.indexOf(this.name);
    dirtySelected.splice(i,1);
    dirtySelected = dirtySelected; // need to assign to trigger Svelte to update value
 }
}
function formSubmit(){
    isWorking(true);
    function _formSubmit(){
        selected = dirtySelected.slice(); // slicing to avoid binding by assignation
        var filters = get(Filters);
        filters[section] = selected;
        //TO DO: all checked is same as none checked
        Filters.set(filters);
        if ( selected.length > 0 ){
            GTMPush('NettingBillions|Filter|' + section + '|' + selected.join('-'));
        }
    }
    if ( window.requestIdleCallback ){
            requestIdleCallback(_formSubmit,{timeout: 500});
        } else {
            setTimeout(_formSubmit);
        }
}
function closeHandler(){
    DimensionFilter.set(undefined);
}
</script>
<style lang="scss">.form-wrapper {
  position: absolute;
  right: 6px;
  top: 7px;
  min-width: 262px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  border: 1px solid #e1e1e1;
  background-color: #fff;
  visibility: hidden;
  z-index: 1;
  width: 100%;
  max-width: 258px; }
  .form-wrapper.isOpen {
    visibility: visible; }

.x-out-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #fff;
  padding-right: 10px; }

.input-wrapper {
  display: flex;
  justify-content: center;
  position: relative; }
  .input-wrapper::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: -26px;
    width: 100%;
    height: 25px;
    background-image: linear-gradient(rgba(255, 255, 255, 0), white, white); }

fieldset {
  border: none;
  margin: 0;
  padding: 0; }

legend {
  color: #333;
  font-weight: bold;
  padding: 0.625rem 9px; }

form {
  max-height: 235px;
  overflow-y: scroll;
  background-color: #fff;
  padding: 0 0 20px 0;
  display: block;
  font-size: 1rem;
  font-weight: normal;
  text-transform: none;
  border-bottom: 1px solid #e1e1e1; }
  form label {
    position: relative;
    display: block;
    color: #333;
    padding: 8px 16px 8px 32px;
    line-height: 100%;
    white-space: nowrap;
    overflow-x: hidden;
    text-overflow: ellipsis; }
    form label:hover {
      background-color: #f0f0f0; }
  form input[type="checkbox"] {
    position: absolute;
    left: 11px;
    top: 11px;
    width: 12px;
    height: 12px;
    background-color: #fff;
    margin: 0;
    padding: 0;
    border: 1px solid #b0b0b0;
    -webkit-appearance: none;
    appearance: none; }
  form input[type="checkbox"]:checked::after {
    top: -1px;
    left: -1px;
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 2px;
    display: block;
    content: " ";
    font-size: .625rem;
    line-height: 1;
    background-color: #296EC3;
    color: #fff;
    text-align: center;
    background: url("data:image/svg+xml,%3Csvg id='Layer_1' data-name='Layer 1' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 7.67 7.97'%3E%3Cpolyline points='0.35 5 2.55 7.19 7.26 0.28' style='fill:none;stroke:%23333;stroke-miterlimit:10'/%3E%3C/svg%3E") 50% 50%/75% 75% no-repeat; }

input[type="submit"] {
  appearance: none;
  align-self: center;
  background-color: #296EC3;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  font-weight: 900;
  width: 130px;
  height: 40px;
  vertical-align: top;
  transition: background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  border: none;
  margin: 10px 5px 0; }
  input[type="submit"][disabled] {
    background-color: #b0b0b0;
    cursor: not-allowed; }
</style>
<div on:click|stopPropagation="{() => {}}" class:isOpen class="form-wrapper">
        <form>
            <fieldset>
                <legend>Select {plural(section)}:</legend>
                {#each data as datum, i}
                <label on:keydown="{keydownHandler}" tabindex="0" data-key="{datum.key}" title="{datum.display}"><input  bind:this="{inputs[i]}" tabindex="-1" on:change="{changeHandler}" name="{datum.key}" type="checkbox" /> {datum.display}</label>
                {/each}
            </fieldset>
        </form>
        <div class="input-wrapper">
            <input disabled="{selected.length == 0}" on:click|preventDefault="{clearAll}" type="submit" value="Clear" />
            <input disabled="{!isDirty}" on:click|preventDefault="{formSubmit}" type="submit" value="Apply changes" />
        </div>
        <div class="x-out-wrapper" on:click|preventDefault="{closeHandler}">
            <XOut ariaLabel="Close {dictionary[section].display} filter options" />
        </div>
</div>