  align-items: center;
  padding-right: 25px;
  margin-right: 25px;
  background-color: #fff; }
  @media screen and (max-width: 585px) {
    .nav-wrapper::before {
      position: absolute;
      top: -1.2rem;
      left: .625rem;
      content: 'Jump to section:';
      color: #767676;
      font-style: italic; } }

nav {
  margin: 0;
  padding: 0;
  flex-grow: 1; }

ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  @media screen and (max-width: 585px) {
    ul {
      flex-direction: column; } }

li {
  list-style: none;
  margin: 0;
  font-size: 2rem;
  font-size: clamp(1.5rem, 3.8vw, 2rem);
  font-weight: bold;
  padding: 0 0.625rem; }

a {
  color: #333;
  transition: color 0.2s ease-in-out;
  text-transform: uppercase; }
  a:active, a:visited {
    color: #333; }
  a:hover {
    color: #296EC3; }
  a[disabled] {
    color: #767676 !important;
    cursor: not-allowed;
    text-decoration: none !important; }
  a.active {
    text-decoration: underline; }

.isHidden {
  visibility: hidden; }
</style>
<div class="nav-wrapper">
    <nav aria-label="Navigation for data visualization section">
        <ul>
            {#each sections as section}
            <li><a class:active="{activeSection && activeSection === section}" disabled="{activeSection && activeSection !== section ? 'disabled' : null}" title="{activeSection && activeSection !== section ? 'Filters applied: view not available' : null}" data-key="{section}" href="#head-{section}">{dictionary[section].display}</a></li>
            {/each}
        </ul>
    </nav>
</div>